@import "../../common/utils/index.scss";

$radio: scopedClass(radio);

.#{$radio} {
  line-height: 1.5em;
  @include inline-flex-start-center;
  cursor: pointer;
  &-inner {
    @include inline-flex-center-center;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    border: 1px solid #ccc;
    margin-right: 4px;
  }
  &-name {
    line-height: 1.5em;
  }
  & + & {
    margin-left: 20px;
  }

  &-checked {
    .#{$radio}-inner {
      border: 1px solid #409eff;
      &:after {
        content: "";
        width: 8px;
        height: 8px;
        background-color: #409eff;
        border-radius: 50%;
        display: inline-block;
      }
    }
  }

  &-disabled {
    cursor: not-allowed;
    color: rgba($color: #000000, $alpha: 0.25);
    .#{$radio}-inner {
      border: 1px solid rgba($color: #000000, $alpha: 0.25);
      &:after {
        background-color: rgba($color: #000000, $alpha: 0.25);
      }
    }
  }

  &-group {
    @include inline-flex-start-center;
  }
}
