[class*="guree"] {
  box-sizing: border-box; }

.guree-span {
  display: inline-flex; }

.guree-switch {
  min-width: 44px;
  min-height: 22px;
  position: relative;
  padding: 2px;
  border-radius: 11px;
  background-color: rgba(0, 0, 0, 0.25);
  color: #fff;
  display: inline-flex;
  cursor: pointer; }
  .guree-switch-handler {
    width: 18px;
    height: 18px;
    position: absolute;
    background-color: #fff;
    left: 2px;
    border-radius: 50%;
    top: 2px;
    transition: all 0.36s cubic-bezier(0.78, 0.14, 0.15, 0.86); }
  .guree-switch-inner {
    min-height: 18px;
    min-width: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    right: 7px;
    top: 2px;
    transition: all 0.36s cubic-bezier(0.78, 0.14, 0.15, 0.86);
    line-height: 18px; }
  .guree-switch-checked {
    background-color: #409eff; }
    .guree-switch-checked .guree-switch-handler {
      left: calc(100% - 18px - 2px); }
    .guree-switch-checked .guree-switch-inner {
      right: 25px; }
  .guree-switch-unvisible {
    visibility: hidden;
    display: inline-flex; }
    .guree-switch-unvisible .guree-switch-handler {
      position: unset; }
    .guree-switch-unvisible .guree-switch-inner {
      position: unset;
      margin-left: 5px;
      margin-right: 5px; }
  .guree-switch-disabled {
    cursor: not-allowed;
    opacity: 0.4; }
    .guree-switch-disabled.guree-switch-checked {
      background-color: rgba(0, 0, 0, 0.25); }
